<template>
	<div id="artProfile">
		<div class="banner" id="back_top">
			<!--<img :src="imgURL+'/userfiles/defaultImg/bjgBanner.jpg'"/>-->
			<img :src="imgURL+bannerBj" />
		</div>
		<div class="content">
			<div class="banner">
				<h1></h1>
				<Photoswipe v-if="isShow" :data="photoData"></Photoswipe>
				<div class="unfurled_describe" :class="{'unfurled_active':isShow}" v-html="info.chinese">

				</div>
				<div class="unfurled_describe" :class="{'unfurled_active':isShow}" v-html="info.english">

				</div>
				<div class="pavilion_impression">
					<span></span>
				</div>
				<Photoswipe v-if="!isShow" :data="photoData"></Photoswipe>

				<!--<span class="more clearfix" @click="handleMore">{{moreInfo}}</span>-->
				<span class="unfurled" @click="handleShow">{{showInfo}}</span>
			</div>
			<div class="title_msg">
				<span></span>
			</div>
			<div class="panorama3D">
				<a href="http://www.artronpano.com/scene/ZTuePeLlkfuPNjz2ZHmVzhpmMzDoVS3M/bjhml//tour.html">
					<img src="../../../assets/image/panorama3D02.jpg" alt="" />
					<div class="panorama3D_mask"></div>
					<div class="exhibition_btn">
						<img src="../../../assets/image/exhibition_btn.png" alt="" class="panorama3D_btn" />
						<span class="panorama3D_dec"></span>
					</div>
				</a>

			</div>
			<div class="exhibition-works" v-if="listData&&listData.length>0">
				<div class="title_msg">
					<span></span>
				</div>
				<ul class="exhibition-img">
					<li v-for="(list, index) in listData" @click="handleJump(list.id)">
						<div class="div_minimg bg_cover" :style="{backgroundImage: 'url('+imgURL+list.thumbPic+')'}">
							<img :src="imgURL+list.thumbPic" @load="successLoadImg" @error="errorLoadImg" />
						</div>
						<span>{{list.name}}</span>
					</li>
					<li class="div-more">
						<div>
							<a href="#libraryWork">
								<i class="icon iconfont icon-gengduo"></i> 
							</a>
						</div>
					</li>
				</ul>
			</div>
			<div class="visit">
				<VisitInformation></VisitInformation>
			</div>
		</div>

	</div>
</template>

<script>
	import VisitInformation from '../../component/visitInformation.vue'
	import Photoswipe from '../../component/photoswipe.vue'

	let abbreviate = {
		chinese: '<p>20062000...</p>',
		english: '<p>In 2006, Mr. Han Meilin donated 2,000 of his works of art to the Tongzhou District Government of Beijing. To house and exhibit this precious gift, to show Mr. Hans love ...<p>'
	};
	let normal = {
		chinese: '<p>2006200010000200862520051019</p><p>20136120002600</p><p></p><p></p><p>2013AAAAAAAA</p><p></p>',
		english: '<p>In 2006, Mr. Han Meilin donated 2,000 of his works of art to the Tongzhou District Government of Beijing. To house and exhibit this precious gift, to show Mr. Hans love of the country and Chinese culture, and to disseminate his aesthetic ideas, the local government built an art gallery in the Liyuan Cultural Theme Park. Two years later, a Palace in ArtBeijing Han Meilin Art Gallery opened to the public on June 25, 2008. It covers an area of nearly 10,000 square meters, combining the ideals of man and nature and integrating Eastern and Western architectural aesthetics. Quiet, spacious and bright, it interweaves time and space and displays unique architectural excellence, further adding cultural significance to the human landscape of the capital city of Beijing. It also shines in concert with the Hangzhou Han Meilin Art Gallery (opened October 19, 2005) located at the other end of the ancient Great Canal. Blending the historic and the modern, this cultural pattern highlights the attraction of the ancient canals history and culture. Mr. Han Meilin, whose father comes from Shandong in northern China and whose mother was born in Zhejiang in southern China, is a patriotic artist with a strong sense of belonging and gratitude to China.</p><p>In addition to the Introductory Hall, Arts and Crafts Hall, Paperboard Ornamental Hall, Sculpture Hall, Chinese Calligraphy and Traditional Painting Hall, Newly-Created Works Hall, Video Hall, and Sculpture-Making Studio, there is the Seal-Book Building, and the Pottery Bar. Such cultural facilities provide visitors with an interactive cultural platform where they can enjoy the beauty of art in genetral as well as that of Chinese civilization.</p><p>Beijing Han Meilin Art Gallery was designed by the famous Chinese architect Kai Cui. His design concept is that the art gallerys site and form have an important impact on Liyuan Parks space as an integral part of the Cultural Theme Park. If we take the whole park as a natural landscape painting, the art gallery serves as a traditional Chinese seal affixed just when the painting is finished. The relation between the seal and the painting corresponds to that of the art gallery and the park. They are indispensible to and complementary to each other. Considering that Chinese calligraphy and seal cutting are also an aspect of Mr. Hans artistic creation, the designer chose the Chinese character for beauty ( mei) as the graphic design prototype of the art gallery. Looking down from above, the shape of the entire art gallery is like a  ; thus the strokes of the Chinese calligraphic character represent the architectures functional space. Significantly, the word beauty ( mei) is also second word of Han Meilins name which can also be understood as representing aesthetics and artistic beauty. These explanations are of the realistic and of the abstract as well.</p><p>Our art gallery adheres to the people-oriented development philosophy, and the staff makes great efforts to provide the public with the most professional, most people-oriented services. In keeping with Mr. Han Meilins noble sentiments, art philosophy and purpose for founding the art gallery, guests have enjoyed free admission to the art gallery since opening day. We will be glad to see more art lovers come to visit this palace of art. Here, visitors can appreciate countless wonderful works of art created by Han Meilin with his open-heated, forthright and sincere love, deep emotion and magic whiteboard marker pens and brushes, all of which most ordinary people cant imagine. Visitors can also experience a great artists life and better understand a versatile artist who has achieved extraordinary success in almost all artistic aspects. Mr. Han is like an active volcano which is incessantly erupting with artistic lava  the flower never ceases. As Mr. Han has said, I just work hard on my art throughout my life, not for money, scholarly honors or official rank. It is Gods way of telling me that since Im as stubborn as an ox, Ill have to work as hard as one.</p>'
	};

	export default {
		data() {
			return {
				bannerBj: pavilionData.bannerBj,
				photoData: pavilionData.artDataybj,
				isShow: false,
				showInfo: '',
				info: abbreviate,
				listData: [],
				//				photoData: photoData,
				imgURL: this.ApiSever.httpUrl,
				imgError: `this.src="${this.ApiSever.defaultUrl}userfiles/defaultImg/defaultMax.jpg"`,
				imgMinError: `this.src="${this.ApiSever.defaultUrl}userfiles/defaultImg/defaultMin.jpg"`
			}
		},
		created() { //
			this.titleInfo = this.$route.meta.name;
		},
		components: {
			VisitInformation,
			Photoswipe
		},
		methods: {
			getLibraryWorkList() {
				let param = {
					pageSize: 3,
					pageNo: 1
				};
				this.ApiSever.introduce.getLibraryWork(param).then(res => {
					let result = res.data;
					if(result.success) {
						this.listData = result.entitys;
					}
				})
			},

			//
			handleShow() {
				this.isShow = !this.isShow;
				if(this.isShow) {
					this.info = Object.assign({}, normal);

					this.showInfo = '';
				} else {
					this.info = Object.assign({}, abbreviate);
					document.body.scrollTop = 0;
					this.showInfo = '';
				}

			},

			//
			handleJump(data) {
				this.$router.push({
					path: '/libraryDetail/' + data,
					params: {
						id: data
					}
				});
			}
		},
		mounted() { //
			this.getLibraryWorkList();
		}
	}
</script>

<style type="text/css" scoped>

</style>


