<template>
	<div id="recruit">
		<form action="" class="recruit-form">
			<div><label></label>
				<input type="text" placeholder="" v-model="springAutumn.name" name="name" @blur="handleBlur" ref="name" />
				<span class="hiden" ref="name_span"></span>
			</div>

			<div><label></label>
				<input type="number" placeholder="" v-model="springAutumn.age" name="age" @blur="handleBlur" ref="age" />
				<span class="hiden" ref="age_span"></span>
			</div>
			<div><label></label>
				<input type="radio" name="sex" v-model="springAutumn.sex" value="1" />
				<input type="radio" name="sex" v-model="springAutumn.sex" value="2" />
			</div>
			<div><label></label>
				<input type="text" placeholder="" v-model="springAutumn.school" name="graduateSchool" @blur="handleBlur" ref="school" />
				<span class="hiden" ref="school_span"></span>
			</div>
			<div><label></label>
				<input type="number" placeholder="" v-model="springAutumn.tel" name="tel" @blur="handleBlur" ref="tel" />
				<span class="hiden" ref="tel_span"></span>
			</div>
			<div><label></label>
				<input type="email" placeholder="" v-model="springAutumn.mail" name="mail" @blur="handleBlur" ref="mail" />
				<span class="hiden" ref="mail_span"></span>
			</div>
			<div><label></label>
				<input type="text" placeholder="" v-model="springAutumn.activities" name="activities" ref="activities" />
				<span class="hiden" ref="activities_span"></span>
			</div>
			<div><label></label>
				<textarea placeholder="" v-model="springAutumn.remarks"></textarea>
			</div>
			<span class="a_herf" @click="addpubliceducation"> </span>

		</form>
	</div>

</template>

<script>
	import { Navbar, TabItem, Field, Radio, Indicator, Toast, MessageBox } from 'mint-ui'

	//
	let rejson = {
		name: /\S/, //
		age: /^(?:[1-9][0-9]?|1[01][0-9]|120)$/,
		tel: /^([0-9]{3,4}-)?[0-9]{7,8}$/, //
		mail: /^\w+@[a-zA-Z0-9\-]+(\.[a-zA-Z]{2,6}){1,2}$/,
		activities: /\S/
	};

	//
	let isMob = /^((\+?86)|(\+86))?(13[012356789][0-9]{8}|15[012356789][0-9]{8}|18[02356789][0-9]{8}|147[0-9]{8}|1349[0-9]{7})$/;

	export default {
		data() {
			return {
				springAutumn: {
					name: '',
					sex: 1,
					age: '',
					school: '',
					tel: '',
					mail: '',
					remarks: '',
					activities: ''
				}
			}
		},
		created() { //
			this.titleInfo = this.$route.meta.name;
			this.getPublicEducationTitle();
		},
		components: {
		},
		methods: {
			//ID
			getPublicEducationTitle() {
				let param = {
					id: this.$route.params.id
				};

				this.ApiSever.service.getPublicEducationDetail(param).then(res => {
					let result = res.data;

					if(result.success) {
						this.springAutumn.activities = result.entity.title;
					}
				});
			},

			//
			handleBlur(event) {
				let currentEl = event.target.name;
				if(rejson[currentEl]) {
					if(currentEl == 'tel') {
						if(rejson[currentEl].test(event.target.value) || isMob.test(event.target.value)) {
							this.$refs[currentEl + '_span'].className = 'hiden';
						} else {
							this.$refs[currentEl + '_span'].className = 'errShow';
						}
					} else {
						if(rejson[currentEl].test(event.target.value)) {
							this.$refs[currentEl + '_span'].className = 'hiden';
						} else {
							this.$refs[currentEl + '_span'].className = 'errShow';
						}
					}
				}
			},

			//
			addpubliceducation() {
				let bOk = true;
				for(let name in rejson) {
					if(name == 'tel') {
						if(rejson[name].test(this.springAutumn[name]) || isMob.test(this.springAutumn[name])) {
							this.$refs[name + '_span'].className = 'hiden';
						} else {
							this.$refs[name + '_span'].className = 'errShow';
							bOk = false;
						}
					} else {
						if(!rejson[name].test(this.springAutumn[name])) {
							this.$refs[name + '_span'].className = 'errShow';
							bOk = false;
						} else {
							this.$refs[name + '_span'].className = 'hiden';
						}
					}
				}

				if(!bOk) {
					return false;
				}

				let param = Object.assign({}, this.springAutumn);
				param.actiId = this.$route.params.id;

				delete param.activities;

				this.ApiSever.service.addpubliceducation(param).then(res => {
					let result = res.data;
					if(result.success) {
						this.$toast({
							message: '',
							iconClass: 'icon icon-success'
						});
						this.$router.push({
							path: '/publicEducation'
						});
					} else {
						switch(result.message) {
							case -2:
								MessageBox.alert('');
								break;
							case -3:
								MessageBox.alert('');
								break;
							case -4:
								MessageBox.alert('');
								break;
							default:
								break;
						}
					}
					//					let result = res.data;
					//					Indicator.close();
					//					if(result.success) {
					//						this.$toast('')
					//						this.$router.push({
					//							path: '/publicEducation'
					//						});
					//					}
				})
			}

		},

		mounted() {

		}
	}
</script>

<style type="text/css" scoped>
	.mint-tab-container {
		background: #fff;
	}
	
	.promptData {
		width: 100%;
		padding-left: 5rem;
		box-sizing: border-box;
		display: block;
		color: red;
		clear: both;
	}
	
	.input_div .errShow {
		padding-left: 0 !important;
	}
	
	.errShow {
		width: 100%;
		height: 0.4375rem;
		line-height: 0.4375rem;
		display: block;
		color: red;
		padding-left: 5rem;
		box-sizing: border-box;
	}
	
	.hiden {
		display: none;
	}
</style>


