<template>

	<div id="myMap">

	</div>

</template>

<script>
	export default {
		data() {
			return {}
		},
		components: { //

		},
		methods: {
			//
			createMap() {
				let self = this;
				var map = new BMap.Map("myMap"); //
				var point = new BMap.Point(116.683709, 39.88139); //
				map.centerAndZoom(point, 17); //
				window.map = map; //map

				var myKeys = [""];
				var local = new BMap.LocalSearch(map, {
					renderOptions: {
						map: map,
						panel: "r-result"
					},
					pageCapacity: 5
				});
				local.searchInBounds(myKeys, map.getBounds());

			},
			//
			setMapEvent() {
				map.enableDragging(); //()
				map.enableScrollWheelZoom(); //
				map.enableDoubleClickZoom(); //()
				map.enableKeyboard(); //
			},
			initMap() {
				this.createMap(); //
				this.setMapEvent(); //

			}

		},
		created() { //

		},
		mounted() {
						this.initMap();

		}
	}
</script>

<style type="text/css" scoped>
	#myMap {
		position: absolute;
		left: 0;
		right: 0;
		top: 0;
		bottom: 0;
		width: 100%;
		height: 100%;
		margin: auto;
		display: block;
		overflow: hidden;
		z-index: 99;
	}
</style>


