<template>
	<div id="dituContent"></div>

</template>

<script>
	export default {
		data() {
			return {}
		},
		components: { //

		},
		methods: {
			//
			createMap() {
				var map = new BMap.Map("dituContent"); //
				var point = new BMap.Point(116.683709, 39.88139); //
				map.centerAndZoom(point, 17); //
				window.map = map; //map
			},

			//
			setMapEvent() {
				map.enableDragging(); //()
				map.enableScrollWheelZoom(); //
				map.enableDoubleClickZoom(); //()
				map.enableKeyboard(); //
			},
			initMap() {
				this.createMap(); //
				this.setMapEvent(); //
			}

		},
		created() { //

		},
		mounted() {
			this.initMap(); //

		}
	}
</script>

<style type="text/css" scoped>
	#dituContent {
		width: 100%;
		height: 100%;
		/*margin-top: 0.265rem;
		margin-bottom: 0.265rem;*/
		display: block;
		overflow: hidden;
	}

</style>


