<template>
	<div id="timeline">

		<div class="content">
			<div class="banner">
				<img :src="imgURL+'/data/userfiles/defaultImg/user.jpg'" />
				<div class="unfurled_describe" :class="{'unfurled_active':isShow}">
					<p>
						19361955</p>
					<p>

						</p>
					<p>

						19802110119836200320042011 20122013122120152016-2017</p>
					<p>

						20082008</p>
					<p>

						</p>
					<p>

						
					</p>
				</div>
				<div class="unfurledEn_describe english" :class="{'unfurled_active':isShow}">
					<p>Han Meilin was born in China in 1936, in the city of Jinan, Shandong Province. He was admitted to the Central Academy of Fine Arts (later renamed Central Academy of Art and Design) in 1955. He is currently a professor at Tsinghua University, Vice Chairman of Tsinghua Universitys Academic Committee, and a PhD advisor at the Graduate School of Chinese National Academy of Arts. He is also a Standing Committee member of the CPPCC National Committee, a member of the Central Research Institute of Culture and History, Director of the Ceramic Arts Committee of China Artists Association, Vice Chair of the United World Chinese Association, Chairman of the Calligraphy and Painting Committee of China National Arts and Crafts Society, Standing Committee member of the China Council for the Promotion of Peaceful Reunification, and a professional writer of the Writing Research Department of the Chinese Writers Association.</p>
					<p>

						Han Meilin is a tireless experimenter and explorer of art. His artworks are extremely diverse; they span the fields of painting, calligraphy, sculpture, ceramics, design, writing, etc. Han Meilins unique artistic style is to draw inspiration from traditional and popular Chinese culture and transform them into art through a contemporary sense of aesthetics.</p>
					<p>

						In 1980, Han Meilins personal touring exhibition was staged in 21 American cities, and he was awarded the key to the city of San Diego in the same year. In Manhattan, New York, October 1 of that year was decreed Han Meilin Day. His animated film, The Fox and the Hunter, received the Best Fine Arts Prize at the Fourth Annual World Festival of Animated Film in Zagreb. In 1983, six pieces of his artwork were selected to feature on Christmas Cards issued by the United Nations. In 2003, he was recognized for his special contribution to world art by the World Artists Association of America. In 2004, he received the World Art Master Prize from the World Artists Association of America and the President's Award for Educational Excellence issued by former American President George W. Bush. In 2011, he was designated the Most Followed Chinese Figure in the World by Global Times and received the Lifetime Achievement Award for Contribution to Chinese Literature and Art from Chinas Ministry of Culture. A Grand Exhibition of Art by Han Meilin was held at the National Museum of China, with unprecedented popularity. In 2012, his sculpture series, Mother and Child, received the Copyright Gold Medal from the World Intellectual Property Organization. On December 21, 2013, the first Han Meilin Day was declared in China. In 2015, UNESCO designated Han Meilin as an Artist of Peace, the first Chinese artist to receive this honor. There are Han Meilin Art Museums in the cities of Hangzhou, Beijing and Yinchuan, further promoting art education and international cultural exchange. From 2016 to 2017, Han Meillin World Tour Exhibition were successfully held in Venice, Beijing and Paris. </p>
					<p>

						His signature works include: A collection of ancient Chinese calligraphy, The Sealed Book; giant city sculptures, Howling at the Wind, Shun Plowing a Field, Birds of Good Luck, The Dragon of Qian River, God of Qian River Shooting at the Tide, Fire Phoenix, The Fire Phoenix Welcoming Harmony, One Hundred Birds Honoring the Phoenix, The Phoenix Honoring the Sun, and The Gate of South Lake; the designs of: the Air China logo, the interior design of a new fleet of Air China airplanes, the logo of the 2008 Beijing Olympic bid, the Fuwa mascots of the 2008 Beijing Olympics, the the logo for the Beijing Tourism Office, the theme for the Hong Kong and Macau travel permit, the logo of the China-Russia Youth Year, the logo for the Lancang-Mekong Cooperation Leaders Meeting, etc.</p>
					<p>

						Han Meilins work is part of the collections of the National Museum of China, the State Russian Museum, the British Museum, the Egyptian Museum, the Institut de France, and the Massachusetts Institute of Technology, among others.</p>
					<p>

						Han Meilins publications include: Mountain Flowers in Full Bloom, Meilin, Self-Selected Sculptures by Han Meilin, Self-Selected Paintings by Han Meilin, Bloom of Beauty: Figure Drawings by Han Meilin, Lessons with Han Meilin: Manuscript of Figure Drawings, Mountain and River: A Collection of Han Meilin's Landscape Paintings, A Collection of Han Meilin Calligraphy, Han Meilin Calligraphy: Freestyle (Five Volumes), Perseverance: Manuscript of Han Meilin Calligraphy, Art by Han Meilin, New Theories on Han Meilins Art, The Book of Rites: Datong, the Heart Sutra: Freestyle, Collection of Han Meilins Rock Paintings, and the essay collections, Some Ideas, Han Meilin in His Own Words, Essays by Han Meilin, and the Future is Unknown, etc.</p>
				</div>
				<span class="unfurled" @click="handleShow">{{showInfo}}</span>
			</div>

			<div class="month-time" v-if="listData&&listData.length>0">
				<div class="stage" v-for="(list, index) in listData" key="index" :id="'year_'+list.year">
					<span :id="'index_'+list.year" class="year" :class="{'year_active':currentYear==list.year}" @click="setcunrrentYear(list.year)">{{list.year}}</span>
					<ul :class="{'show_active':currentYear==list.year}">
						<li>
							<div class="work_msg" v-if="list.monthEntity&&list.monthEntity.length>0" v-for="(children, item) in list.monthEntity" key="item">
								<span class="span_mouse" v-if="children.month"><i class="iconfont_dian"></i>{{children.month}}</span>
								<span class="span_mouse" v-if="!children.month"></span>
								<div class="msg_div">
									<a href="javascript:;" v-for="(data, key) in children.entitys" key="key" @click="handleJump(data.id)">
										<span><i class="iconfont">&#xe6e0;</i> {{data.title}}</span>
										<div class="box_work">
											<ol v-if="data.thumbPic||data.thumbPic2||data.thumbPic3">
												<li v-if="data.thumbPic" class="div_minimg bg_cover" :style="{backgroundImage: 'url('+imgURL+data.thumbPic+')'}">
													<img :src="imgURL+data.thumbPic" @load="successLoadImg" @error="errorLoadImg" />
												</li>
												<li v-if="data.thumbPic2" class="div_minimg bg_cover" :style="{backgroundImage: 'url('+imgURL+data.thumbPic2+')'}">
													<img :src="imgURL+data.thumbPic2" @load="successLoadImg" @error="errorLoadImg" />
												</li>
												<li v-if="data.thumbPic3" class="div_minimg bg_cover" :style="{backgroundImage: 'url('+imgURL+data.thumbPic3+')'}">
													<img :src="imgURL+data.thumbPic3" @load="successLoadImg" @error="errorLoadImg" />
												</li>
												<i class="iconfont user_icont">&#xe601;</i>
											</ol>
										</div>
									</a>
								</div>
							</div>
						</li>

					</ul>
				</div>
			</div>
		</div>
	</div>

</template>

<script>
	import { Indicator } from 'mint-ui'

	export default {
		data() {
			return {
				isShow: false,
				showInfo: '',
				currentYear: '2017',
				listData: [],
				imgURL: this.ApiSever.httpUrl,
				imgError: `this.src="${this.ApiSever.defaultUrl}userfiles/defaultImg/defaultMax.jpg"`,
				imgMinError: `this.src="${this.ApiSever.defaultUrl}userfiles/defaultImg/defaultMin.jpg"`
			}
		},
		created() {},
		components: {},
		methods: {
			//
			getUserInfo() {
				let self = this;
				Indicator.open('...');
				this.ApiSever.introduce.getUserInfo(null).then(res => {
					Indicator.close();
					let result = res.data;
					if(result.success) {
						self.listData = result.entitys
						if(sessionStorage.year) {
							self.currentYear = sessionStorage.year;
						} else {
							self.currentYear = result.entitys[0].year;
							sessionStorage.anchorPoint = '';
						}
						if(sessionStorage.anchorPoint) {
							let currentId = '#' + sessionStorage.anchorPoint;
							setTimeout(() => {
								self.goAnchor(currentId);
							}, 100)

						}
					}
				});
			},

			//
			handleShow() {
				this.isShow = !this.isShow;
				if(this.isShow) {
					this.showInfo = '';
				} else {
					this.showInfo = '';
					document.body.scrollTop = 0;
				}
			},

			//
			setcunrrentYear(data) {
				let self=this;
				this.currentYear = data;
				let currentId = '#index_' + data;
				setTimeout(() => {
					self.goAnchor(currentId);
				}, 50);

			},
			handleJump(data) {
				sessionStorage.year = this.currentYear;
				sessionStorage.anchorPoint = 'year_' + this.currentYear;
				this.$router.push({
					path: '/userdetail/' + data,
					params: {
						id: data
					}
				});
			}
		},

		mounted() { //
			this.getUserInfo();
		},
		destroyed() {
			Indicator.close();
		}
	}
</script>

<style type="text/css" scoped>
	.stage {
		margin-bottom: 1.875rem;
	}
</style>


