import Vue from 'vue'
const HOST = HOST_CONFIG + 'webservice';
//2017041015:02:01 :827879040@qq.com vue
//
Vue.filter("sum", function(value) { // Vue.filter() ,Vue
	return value + 40;
});
Vue.filter("json", function(value) { // Vue.filter() ,Vue
	console.log(value)
	return JSON.stringify(value);
});

Vue.filter("checkSrc", function(value) {

	let img = new Image();
	img.src = HOST + value;
	img.onload = function() {
		console.log('dddd', value);
		return HOST + value
	};
	img.onerror = function() {
		return 'http://www.artrondata.com/hml/view/26%E3%80%81%E5%9C%9F%E9%99%B6%E8%80%81%E8%99%8E.JPG'
	};
	//			image.src = path;

	//	console.log(value)
	//	return value
});


